import 'babel-polyfill'
import Vue from 'vue'
import App from './App.vue'
import router from './router'

import Http from './http'

// 引入全局样式
import './assets/styles/global.scss'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

import moment from 'moment'

import echarts from 'echarts'

// Base64
import base64 from 'js-base64'

import md5 from 'js-md5'

import { sessionData, getUser, getData, setData, removeData } from './utils/storage'
import { toBottom, toNext, toPrev, toTop } from './utils/sort'
import { jumpToFirst, jumpToLast, formatCode, roleJoin, deepClone, alarmType, handleState, mustSel } from './utils/util'
// 数值计算
import { accAdd, accSub, accMul, accDiv } from './utils/xmath'

Vue.use(ElementUI)
Vue.use(Http)

Vue.config.productionTip = false
Vue.prototype.$errorTip = function (msg) {
  this.$message({ duration: 2000, message: msg, type: 'error', offset: 200 })
}
Vue.prototype.$infoTip = function (msg) {
  this.$message({ duration: 2000, message: msg, type: 'info', offset: 200 })
}
const settings = require('@/settings')
Vue.prototype.$picUpload = settings.PictureUploadUrl
Vue.prototype.$picDownload = settings.PictureDownloadUrl
Vue.prototype.$baseRequestUrl = settings.KafBaseUrl
Vue.prototype.$templateDownloadUrl = settings.TemplateDownloadUrl
Vue.prototype.$wsUri = settings.WS_URI

Vue.prototype.$moment = moment

Vue.prototype.$echarts = echarts

Vue.prototype.$base64 = base64.Base64

Vue.prototype.$md5 = md5

Vue.prototype.$sessionData = sessionData // this.$sessionData("方法","名字",数据); method：get获取，set存入或覆盖，remove清除
Vue.prototype.$getUser = getUser // 取user某一个key this.$getUser('storeId')  取整个user this.$getUser()
Vue.prototype.$getData = getData // 取值 this.$getData('id')
Vue.prototype.$setData = setData // 赋值 this.$getData('id', 2)
Vue.prototype.$removeData = removeData // 清除 this.$getData('id')

Vue.prototype.$toBottom = toBottom
Vue.prototype.$toNext = toNext
Vue.prototype.$toPrev = toPrev
Vue.prototype.$toTop = toTop

Vue.prototype.$jumpToFirst = jumpToFirst
Vue.prototype.$jumpToLast = jumpToLast
Vue.prototype.$formatCode = formatCode
Vue.prototype.$roleJoin = roleJoin
Vue.prototype.$deepClone = deepClone
Vue.prototype.$alarmType = alarmType
Vue.prototype.$handleState = handleState
Vue.prototype.$mustSel = mustSel

Vue.prototype.$accAdd = accAdd
Vue.prototype.$accSub = accSub
Vue.prototype.$accMul = accMul
Vue.prototype.$accDiv = accDiv

Vue.prototype.$eventBus = new Vue()

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
