// 跳首页
const jumpToFirst = (obj, fn) => {
  obj.page = 1
  return fn()
}

// 跳尾页
const jumpToLast = (obj, fn) => {
  const page = Math.ceil(obj.total / obj.size)
  obj.page = page
  return fn()
}

const formatCode = (length) => {
  let n = 3
  let num = length + 1 // 当前数据data长度
  let len = num.toString().length // 数据长度的length
  while (len < n) {
    num = '0' + num
    len++
  }
  return num
}

const roleJoin = (menuArr) => {
  let str = ''
  let tempArr = []
  let numList = [] // 一级数字
  let index = 0 // 首页 parentId = 0 过滤掉 0所在索引
  menuArr.forEach(item => {
    tempArr.push(item.id, item.parentId)
  })
  numList = quChongfn(tempArr)
  console.log(numList)
  index = numList.indexOf(0)
  index > -1 && numList.splice(index, 1)
  return numList
}
function quChongfn(arr) {
  let newArr = []
  for (let i = 0; i < arr.length; i++) {
    let n = arr[i]
    if (newArr.indexOf(n) == -1) {
      newArr.push(n)
    }
  }
  return newArr
}


// 深拷贝
const deepClone = (origin) => {
  if (!isObject(origin)) return origin
  var target = isArray(origin) ? [] : {}
  for (var prop in origin) {
    if (origin.hasOwnProperty(prop)) {
      var value = origin[prop]
      target[prop] = isObject(value) ? deepClone(value) : value
    }
  }
  function isObject(any) {
    return typeof any == 'object' && any != null
  }
  function isArray(any) {
    return Array.isArray(any)
  }
  return target
}

// 报警类型
const alarmType = alarmType => {
  switch (alarmType) {
    case 'W':
      return '水量报警'
    case 'C':
      return '纸杯报警'
    case 'L':
      return '杯盖报警'
    case 'S':
      return '料仓报警'
    case 'E':
      return '料仓电机报警'
    case 'D':
      return '自动门报警'
    case 'M':
      return '电磁阀报警'
    case 'B':
      return '搅拌器报警'
    case 'O':
      return '其他报警'
    default:
      return '未知'
  }
}

// 报警状态
const handleState = handleState => {
  switch (handleState) {
    case 'C':
      return '新增任务'
    case 'R':
      return '接受任务'
    case 'D':
      return '拒绝任务'
    case 'F':
      return '完成任务'
    case 'E':
      return '清除警报'
    default:
      return '未知'
  }
}
// 联想输入必须选择
const mustSel = (that, selId, selName) => {
  if (!that[selId]) that[selName] = null
}
export { jumpToFirst, jumpToLast, formatCode, roleJoin, deepClone, alarmType, handleState, mustSel }
