import axios from 'axios'
import { Message, Loading } from 'element-ui'
import Vue from 'vue'
const that = new Vue()
// import ca from 'element-ui/src/locale/lang/ca'
// 在config.js文件中统一存放一些公共常量，方便之后维护
// import { API_BASE_URL } from './settings.js'
const settings = require('./settings')
// 添加请求拦截器，在发送请求之前做些什么(**具体查看axios文档**)--------------------------------------------
axios.interceptors.request.use(
  config => {
    // 显示loading
    if (!config.silence) {
      startLoading()
    }
    return config
  },
  error => {
    startLoading()
    // 请求错误时弹框提示，或做些其他事
    return Promise.reject(error)
  }
)

// 添加响应拦截器(**具体查看axios文档**)----------------------------------------------------------------
axios.interceptors.response.use(
  response => {
    // 对响应数据做点什么，允许在数据返回客户端前，修改响应的数据
    // 如果只需要返回体中数据，则如下，如果需要全部，则 return response 即可
    endLoading()
    return response.data
  },
  error => {
    endLoading()
    // debugger
    // 对响应错误做点什么
    return Promise.reject(error)
  }
)

// 封装数据返回失败提示函数---------------------------------------------------------------------------
const errorState = response => {
  // 隐藏loading
  // 如果http状态码正常，则直接返回数据
}

// 封装数据返回成功提示函数---------------------------------------------------------------------------
const successState = res => {
  // 隐藏loading
  // 统一判断后端返回的错误码(错误码与后台协商而定)
  if (res.data.code === '200') {
    console.log('success')
    return res
  }
}

// 封装axios--------------------------------------------------------------------------------------
const apiAxios = (method, url, params, connectionTimeout, silence) => {
  // console.debug('url=' + url)
  const tokenId = that.$getUser('tokenId')
  const user = that.$sessionData('get', 'USER')
  // console.log(user)
  const httpDefault = {
    silence: silence,
    method: method,
    baseURL: settings.KafBaseUrl,
    url: url + (url.indexOf('?') === -1 ? '?' : '&') + 'r=' + Math.random(),
    headers: {
      'Content-Type': 'application/json',
      'X-TOKEN-ID': tokenId || ''
      // token: tokenId || '',
      // 'X-ENTERPRISE-ID':user&&user.enterpriseId,
      // 'X-USER-ID':user&&user.userId
    },
    // `params` 是即将与请求一起发送的 URL 参数
    // `data` 是作为请求主体被发送的数据
    params: method === 'GET' || method === 'DELETE' ? params : null,
    data: method === 'POST' || method === 'PUT' ? params : null,
    timeout: connectionTimeout ? 60000000 : 10000
  }

  // 注意**Promise**使用(Promise首字母大写)
  return new Promise((resolve, reject) => {
    axios(httpDefault)
      // 此处的.then属于axios
      .then(res => {
        const returnMessage = res.message
        switch (res.code) {
          case '0':
            // Message.success(returnMessage)
            resolve(res.data)
            break
          case 'session.expire':
            console.log('会话过期')
            location.href = location.hash ? (location.hash.indexOf('?') !== -1 ? location.origin + location.pathname + '#/login?' + location.hash.split('?')[1] : location.origin + location.pathname + '#/login?' + location.hash.slice(2)) : location.origin + location.pathname + 'login?' + location.pathname.slice(1)
            that.$sessionData('remove', 'USER')
            errorState(returnMessage)
            Message.error(returnMessage)
            reject(returnMessage)
            break
          case 'param.valid':
            console.log('入参验证失败')
            errorState(returnMessage)
            Message.error(returnMessage)
            reject(returnMessage)
            break
          case 'url.hystrix':
            console.log('接口降级，熔断')
            errorState(returnMessage)
            Message.error(returnMessage)
            reject(returnMessage)
            break
          case 'db.error':
            console.log('数据库SQL执行错误')
            errorState(returnMessage)
            Message.error(returnMessage)
            reject(returnMessage)
            break
          default:
            console.log('其他错误')
            errorState('其他错误')
            Message.error(returnMessage)
            reject(returnMessage)
        }
      })
      .catch(error => {
        //
        // switch (error.response.status) {
        //   case 400:
        //   case 401:
        //     break
        //   case 500:
        //     break
        // }
        console.log('网络请求失败:' + url)
        errorState('其他错误')
        Message.error('网络请求失败')
        reject('网络请求失败')
      })
  })
}

let loading
// 内存中正在请求的数量
let loadingNum = 0
function startLoading() {
  if (loadingNum == 0) {
    loading = Loading.service({
      lock: true,
      text: '拼命加载中...',
      background: 'rgba(0, 0, 0, 0.5)'
    })
  }
  // 请求数量加1
  loadingNum++
}
function endLoading() {
  // 请求数量减1
  loadingNum--
  if (loadingNum <= 0) {
    if (loading) {
      loading.close()
    }
  }
}

// 输出函数getAxios、postAxios、putAxios、deleteAxios，供其他文件调用-----------------------------
// Vue.js的插件应当有一个公开方法 install。这个方法的第一个参数是 Vue 构造器，第二个参数是一个可选的选项对象。
export default Vue => {
  Vue.prototype.httpGet = (url, params, connectionTimeout, silence) => apiAxios('GET', url, params, connectionTimeout, silence)
  Vue.prototype.httpPost = (url, params, connectionTimeout, silence) => apiAxios('POST', url, params, connectionTimeout, silence)
  Vue.prototype.httpPut = (url, params) => apiAxios('PUT', url, params)
  Vue.prototype.httpDelete = (url, params) => apiAxios('DELETE', url, params)
}
